import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { formatGram, getUserdata, hiddenRecod, localStoragedecryp } from "../../../../../components/helpers/function";
class LaporanExelPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let stock_on_hand = 0;
    let berat = 0;
    let berat_atribut = 0;
    let total_barang = 0;
    let harga_jual = 0;
    let harga_atribut = 0;
    let berat_selisih = 0;
    let harga_gram = 0;
    let harga_total = 0;
    let ongkos = 0;

    
    let total_pembayaran_cash = 0;
    let total_pembayaran_transfer = 0;
    let total_pembayaran_debit = 0;
    let total_pembayaran_kredit= 0;
    let total_pembayaran_tukar= 0;

    this.props.data.forEach((row, index) => {
      row.pembayaran.forEach((data_pembayaran, index) => {
        total_pembayaran_cash =  data_pembayaran.jenis === "CASH" ? parseInt(total_pembayaran_cash) + parseInt(data_pembayaran.jumlah_in) : parseInt(total_pembayaran_cash);
        total_pembayaran_transfer  = data_pembayaran.jenis === "TRANSFER" ? parseInt(total_pembayaran_transfer) + parseInt(data_pembayaran.jumlah_in) : parseInt(total_pembayaran_transfer);
        total_pembayaran_debit = data_pembayaran.jenis === "DEBET" ? parseInt(total_pembayaran_debit) + parseInt(data_pembayaran.jumlah_in) : parseInt(total_pembayaran_debit);
        total_pembayaran_kredit = data_pembayaran.jenis === "CREDIT" ? parseInt(total_pembayaran_kredit) + parseInt(data_pembayaran.jumlah_in) : parseInt(total_pembayaran_kredit);
        total_pembayaran_tukar = data_pembayaran.jenis === "TUKAR" ? parseInt(total_pembayaran_tukar) + parseInt(data_pembayaran.jumlah_in) : parseInt(total_pembayaran_tukar);
      })
    })
    
    this.props.data.forEach((barang, index) => {
    
      barang.detail.forEach((item, index) => {
        stock_on_hand = stock_on_hand + item.stock_on_hand;
        berat_atribut = berat_atribut + item.berat_atribut;
        berat = berat + item.berat;
        harga_jual = harga_jual + item.harga_jual;
        ongkos = ongkos + item.ongkos;
        harga_total = harga_total + item.harga_total;
        harga_gram = harga_gram + item.harga_gram;
        harga_atribut = harga_atribut + item.harga_atribut;
        berat_selisih = berat_selisih + item.berat_selisih;
      });
      total_barang = total_barang + barang.detail.length;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Transaksi Penjualan"
          sheet="Laporan Transaksi Penjualan"
          buttonText="Export Exel"
        />
        <table id="table-to-xls" style={{ display: "none" }}>
          <thead>
            <tr>
              <th colSpan="15" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN TRANSAKSI PENJUALAN{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="15"> Periode </th>
            </tr>
            <tr>
              <th colSpan="15">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    " s/d " +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="15"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="15"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="15"></th>
            </tr>
            <tr>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NO FJ
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE BARCODE
              </td>
              {/* <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE MEMBER
              </td> */}
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA MEMBER
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA BARANG
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BERAT
              </td>
              {this.props.type_laporan === "SEMUA" ? null :
                <>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                    BERAT SELISIH
                  </td>
                </>
              }
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                ONGKOS
              </td>
              
              {/* <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA ATRIBUT
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BERAT ATRIBUT
              </td> */}
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                HARGA ATRIBUT
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                HARGA GRAM
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                HARGA JUAL
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                HARGA TOTAL
              </td>
              
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                CASH
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                TRANSFER
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                CREDIT
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                DEBET
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                TUKAR
              </td>
            </tr>
          </thead>
          {this.props.data.map((item, index) => (
            <>
              <tbody>
                {item.detail.map((row, index) => (
                  <tr>
                    <td>
                      {row.no_faktur_jual}
                    </td>
                    <td>
                      &nbsp;{row.kode_barcode}
                    </td>
                    {/* <td>
                      {row.kode_member}
                    </td> */}
                    <td>
                      {row.nama_customer}
                    </td>
                    <td>
                      {row.nama_barang}
                    </td>
                    <td style={{textAlign:"right"}}>
                      {formatGram(row.berat,2)}
                    </td>
                    {this.props.type_laporan === "SEMUA" ? null :
                        <td style={{textAlign:"right"}}>
                        {formatGram(row.berat_selisih,2)}
                      </td>
                    }
                    <td style={{textAlign:"right"}}>
                      {hiddenRecod(row.ongkos).toLocaleString("kr-KO")}
                    </td>
                  
                    {/* <td>
                      {row.nama_atribut}
                    </td>
                    <td>
                      {formatGram(row.berat_atribut,3)}
                    </td> */}
                    <td style={{textAlign:"right"}}>
                      {row.harga_atribut.toLocaleString("kr-KO")}
                    </td>
                    <td style={{textAlign:"right"}}>
                      {row.harga_gram.toLocaleString("kr-KO")}
                    </td>
                    <td style={{textAlign:"right"}}>
                      {row.harga_jual.toLocaleString("kr-KO")}
                    </td>
                    <td style={{textAlign:"right"}}>
                      {row.harga_total.toLocaleString("kr-KO")}
                    </td>
                    <td style={{textAlign:"right"}}>
                      {item.pembayaran === undefined ? 0 :item.pembayaran.filter((data) => data.jenis === "CASH").length !== 0 ? item.pembayaran.filter((data) => data.jenis === "CASH")[0].jumlah_in.toLocaleString("kr-KO") : 0}
                    </td>
                    <td style={{textAlign:"right"}}>
                      {item.pembayaran === undefined ? 0 :item.pembayaran.filter((data) => data.jenis === "TRANSFER").length !== 0 ? item.pembayaran.filter((data) => data.jenis === "TRANSFER")[0].jumlah_in.toLocaleString("kr-KO") : 0}
                    </td>
                    <td style={{textAlign:"right"}}>
                      {item.pembayaran === undefined ? 0 :item.pembayaran.filter((data) => data.jenis === "CREDIT").length !== 0 ? item.pembayaran.filter((data) => data.jenis === "CREDIT")[0].jumlah_in.toLocaleString("kr-KO") : 0}
                    </td>
                    <td style={{textAlign:"right"}}>
                      {item.pembayaran === undefined ? 0 :item.pembayaran.filter((data) => data.jenis === "DEBET").length !== 0 ? item.pembayaran.filter((data) => data.jenis === "DEBET")[0].jumlah_in.toLocaleString("kr-KO") : 0}
                    </td>
                    <td style={{textAlign:"right"}}>
                      {item.pembayaran === undefined ? 0 :item.pembayaran.filter((data) => data.jenis === "TUKAR").length !== 0 ? item.pembayaran.filter((data) => data.jenis === "TUKAR")[0].jumlah_in.toLocaleString("kr-KO") : 0}
                    </td>
                  </tr>
                ))}
              </tbody>
              <tr>
                <td colSpan="4" style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign:"center" }}>
                Total Barang {item.detail.length}
                </td>
                <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                  {" "}
                  {formatGram(item.detail
                    .map((list) => list.berat)
                    .reduce((a, b) => a + b, 0),2)}{" "}
                </td>
                {this.props.type_laporan === "SEMUA" ? null :
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                    {" "}
                    {formatGram(item.detail
                      .map((list) => list.berat_selisih)
                      .reduce((a, b) => a + b, 0),2)}{" "}
                  </td>
                }
                <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign:"right" }}>
                  {" "}
                  {item.detail
                    .map((list) => hiddenRecod(list.ongkos).toLocaleString("kr-KO"))
                    .reduce((a, b) => a + b, 0)}{" "}
                </td>
              
            
                <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign:"right" }}>
                  {" "}
                  {item.detail
                    .map((list) => list.harga_atribut)
                    .reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}{" "}
                </td>
                <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign:"right" }}>
                  {" "}
                  {item.detail
                    .map((list) => list.harga_gram)
                    .reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}{" "}
                </td>
                <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign:"right" }}>
                  {" "}
                  {item.detail
                    .map((list) => list.harga_jual)
                    .reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}{" "}
                </td>
                <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign:"right" }}>
                  {" "}
                  {item.detail
                    .map((list) => list.harga_total)
                    .reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}{" "}
                </td>
                <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
                  {item.pembayaran === undefined ? 0 :item.pembayaran.filter((data) => data.jenis === "CASH").length !== 0 ? item.pembayaran.filter((data) => data.jenis === "CASH")[0].jumlah_in.toLocaleString("kr-KO") : 0}
                </td>
                <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
                {item.pembayaran === undefined ? 0 :item.pembayaran.filter((data) => data.jenis === "TRANSFER").length !== 0 ? item.pembayaran.filter((data) => data.jenis === "TRANSFER")[0].jumlah_in.toLocaleString("kr-KO") : 0}
                </td>
                <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
                {item.pembayaran === undefined ? 0 :item.pembayaran.filter((data) => data.jenis === "CREDIT").length !== 0 ? item.pembayaran.filter((data) => data.jenis === "CREDIT")[0].jumlah_in.toLocaleString("kr-KO") : 0}
                </td>
                <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
                {item.pembayaran === undefined ? 0 :item.pembayaran.filter((data) => data.jenis === "DEBET").length !== 0 ? item.pembayaran.filter((data) => data.jenis === "DEBET")[0].jumlah_in.toLocaleString("kr-KO") : 0}
                </td>
                <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
                {item.pembayaran === undefined ? 0 :item.pembayaran.filter((data) => data.jenis === "TUKAR").length !== 0 ? item.pembayaran.filter((data) => data.jenis === "TUKAR")[0].jumlah_in.toLocaleString("kr-KO") : 0}
                </td>
              </tr>
            </>
          ))}
          <tr>
            <td colSpan="4" style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign :"center" }}>
              Grand Total : {total_barang} 
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
              {formatGram(hiddenRecod(berat),3)}
            </td>
            {this.props.type_laporan === "SEMUA" ? null :
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {formatGram(hiddenRecod(berat_selisih), 3)}
              </td>
            }
            <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
              {hiddenRecod(ongkos).toLocaleString("kr-KO")}
            </td>
           
            {/* <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}> </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
              {berat_atribut}
            </td> */}
            <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
              {hiddenRecod(harga_atribut).toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
              {hiddenRecod(harga_gram).toLocaleString("kr-KO")}
            </td>
             <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
              {harga_jual.toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000" , textAlign: "right"}}>
              {hiddenRecod(harga_total).toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
              {hiddenRecod(total_pembayaran_cash).toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
              {hiddenRecod(total_pembayaran_transfer).toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
              {hiddenRecod(total_pembayaran_kredit).toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
              {hiddenRecod(total_pembayaran_debit).toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>
              {hiddenRecod(total_pembayaran_tukar).toLocaleString("kr-KO")}
            </td>
          </tr>
        </table>
      </>
    );
  }
}
export default LaporanExelPenjualan;
